<template>
    <div>
        <div style="text-align: right;">
            <el-button type="success" id="add" @click="dialogFormVisible = true">添加</el-button>
        </div>
        <el-table :data="tableData" stripe style="width: 100%">
            <el-table-column prop="catid" label="ID" width="180">
            </el-table-column>
            <el-table-column prop="name" label="名称" width="180">
            </el-table-column>
            <el-table-column label="操作">
                <template slot-scope="scope">
                    <el-button size="mini" @click="httptest">编辑</el-button>
                    <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <!-- <el-button type="text" @click="dialogFormVisible = true">打开嵌套表单的 Dialog</el-button> -->

        <el-dialog title="添加商品类型" :visible.sync="dialogFormVisible">
            <el-form :model="form">
                <el-form-item label="活动名称" :label-width="formLabelWidth">
                    <el-input v-model="form.name" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="活动区域" :label-width="formLabelWidth">
                    <el-select v-model="form.region" placeholder="请选择活动区域">
                        <el-option label="区域一" value="shanghai"></el-option>
                        <el-option label="区域二" value="beijing"></el-option>
                    </el-select>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible = false">取 消</el-button>
                <el-button type="primary" @click="dialogFormVisible = false">确 定</el-button>
            </div>
        </el-dialog>

    </div>
</template>

<script>
    export default {
        data() {
            return {
                tableData: [{
                    catid: 'catid',
                    name: 'name',
                }],
                dialogFormVisible: false,
                form: {
                    name: '',
                    region: '',
                    date1: '',
                    date2: '',
                    delivery: false,
                    type: [],
                    resource: '',
                    desc: ''
                },
                formLabelWidth: '120px'
            }
        },
        methods: {
            addCategory: function () {
                this.$axios.get('/#/Product')
                    .then(response => {
                        console.log(response)
                    }).catch((response) => {
                        console.log(response)
                    });
            }
        },
        mounted: function () {
            let categoryArray;
            this.$axios.get('/backend/categories')
                .then(response => {
                    categoryArray = response;
                }).catch((response) => {
                    console.log(response)
                });
            categoryArray.forEach(element => {
                let category;
                category.catid = element.categoryId;

            });
        }
    }

</script>
<style>
    #add {
        margin: 20px 20px;
    }
</style>